body {
  font-family: 'Poppins', sans-serif;
  padding: 0;
  margin: 0; }

header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 50px;
  background-color: #333;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center; }
  header ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    list-style: none; }
    header ul li a {
      margin: 0 5px; }
    header ul li a {
      text-decoration: none;
      color: #fff; }
    header ul li a:hover {
      text-decoration: underline;
      color: #646363; }

#intro {
  background-color: #333e6d;
  overflow: hidden; }
  #intro .wrapper {
    display: -ms-grid;
    display: grid;
    grid-gap: 10px;
    -ms-grid-columns: (1fr)[2];
    grid-template-columns: repeat(2, 1fr); }
  #intro .container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    height: 94.8vh;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: end;
    -ms-flex-align: end;
    align-items: flex-end;
    color: #fff; }
    #intro .container .inner-text h1 {
      font-size: 2rem;
      margin-bottom: 5px; }
    #intro .container .skills {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      font-size: 1.2rem; }
  #intro .about-me {
    display: -ms-grid;
    display: grid;
    -ms-grid-columns: 1fr;
    grid-template-columns: 1fr;
    grid-gap: 60px;
    color: #fff; }
    #intro .about-me div:nth-child(odd) {
